 
 
import { POST_TYPES_TELEFONOS } from '../../../actions/categoriesAction/telephones/smartphone';
import { EditData, DeleteData } from '../../../actions/globalTypes';
 
   
   
 
 const initialState = {
    loading: false,
    telefonos: [], // Cambiar `posts` a `telefonos`
    result: 0,
    page: 2,
};

const telefonoReducer = (state = initialState, action) => {
    switch (action.type) {
        case POST_TYPES_TELEFONOS.CREATE_POST_TELEFONO:
            return {
                ...state,
                telefonos: [action.payload, ...state.telefonos],
            };
        case POST_TYPES_TELEFONOS.LOADING_POST:
            return {
                ...state,
                loading: action.payload,
            };
        case POST_TYPES_TELEFONOS.GET_POSTS_TELEFONOS :
            return {
                ...state,
                telefonos: action.payload.telefonos, // Cambiar `posts` a `telefonos`
                result: action.payload.result,
                page: action.payload.page,
            };
        case POST_TYPES_TELEFONOS.UPDATE_POST_TELEFONO:
            return {
                ...state,
                telefonos: EditData(
                    state.telefonos,
                    action.payload._id,
                    action.payload
                ),
            };
        case POST_TYPES_TELEFONOS.DELETE_POST_TELEFONO:
            return {
                ...state,
                telefonos: DeleteData(state.telefonos, action.payload._id),
            };
        default:
            return state;
    }
};

 

 
 
 export default telefonoReducer