
import Carousel from '../../Carousel'

const CardBody = ({ post }) => {

  return (
    <div className="card_body">


      <div className="mb-4">
        {post.attributes && Object.keys(post.attributes).length > 0 ? (
          Object.entries(post.attributes).map(([key, value]) => (
            <li key={key}>
              {key}: {value}
            </li>
          ))
        ) : (
          <li>No hay atributos disponibles</li>
        )}


      </div>

      sub category{post.category}
      category   {post.subCategory}
      {
        post.images.length > 0 && <Carousel images={post.images} id={post._id} />
      }
    </div>
  )
}

export default CardBody
