import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import PostCard from '../../../PostCard'

import LoadIcon from '../../../../images/loading.gif'
import LoadMoreBtn from '../../../LoadMoreBtn'
import { getDataAPI } from '../../../../utils/fetchData'
import { POST_TYPES_VENTE } from '../../../../redux/actions/categoriesAction/immobilier/ImmobilierVenteAction'
 
const PostsImmobilier = () => {
    const { homeImmobilier, auth, theme } = useSelector(state => state)
    const dispatch = useDispatch()

    const [load, setLoad] = useState(false)

    const handleLoadMore = async () => {
        setLoad(true)
        const res = await getDataAPI(`posts?limit=${homeImmobilier.page * 9}`, auth.token)

        dispatch({
            type: POST_TYPES_VENTE.GET_POSTS, 
            payload: {...res.data, page: homeImmobilier.page + 1}
        })

        setLoad(false)
    }

    return (
        <div className="posts">
            {
                homeImmobilier.posts.map(post => (
                    <PostCard key={post._id} post={post} theme={theme} />
                ))
            }

            {
                load && <img src={LoadIcon} alt="loading" className="d-block mx-auto" />
            }

            
            <LoadMoreBtn result={homeImmobilier.result} page={homeImmobilier.page}
            load={load} handleLoadMore={handleLoadMore} />
        </div>
    )
}

export default PostsImmobilier
