 import React from 'react'
import { useSelector } from 'react-redux'
import Vente from '../../../../components/categories/immobilierr/Vente'
 
 const VentePage = () => {
    const {status} = useSelector(state=>state)
   return (
     <div>
       {status && <Vente/>}
     </div>
   )
 }
 
 export default VentePage
 