import { GLOBALTYPES } from '../../globalTypes'
import { imageUpload } from '../../../../utils/imageUpload'
 import { postDataAPI, getDataAPI, patchDataAPI, deleteDataAPI  } from '../../../../utils/fetchData'
import { createNotify,removeNotify } from '../../notifyAction'

  
 
export const POST_TYPES_TELEFONOS= {
    CREATE_POST_TELEFONO: 'CREATE_POST_TELEFONO',
    LOADING_POST: 'LOADING_POST',
    GET_POSTS_TELEFONOS: 'GET_POSTS_TELEFONOS',
    UPDATE_POST_TELEFONO: 'UPDATE_POST_TELEFONO',
    GET_POSTS_TELEFONO: 'GET_POST',
    DELETE_POST_TELEFONO: 'DELETE_POST_TELEFONO'
}
export const updatePostSmartphone = ({ telephoneData, images, auth, smartphone }) => async (dispatch) => {
    let media = [];
    const imgNewUrl = images.filter(img => !img.url);
    const imgOldUrl = images.filter(img => img.url);
 

    if (
        smartphone.subCategorySmartphone === telephoneData.subCategorySmartphone &&
  
        imgNewUrl.length === telephoneData === 0 &&
        imgOldUrl.length === telephoneData.smartphone.images.length
    ) {
      return;
    }
 try {
        dispatch({ type: GLOBALTYPES.ALERT, payload: { loading: true } });
        console.log("Iniciando subida de imgenes nuevas...");
   if (imgNewUrl.length > 0) {
            media = await imageUpload(imgNewUrl);
             }
        const res = await patchDataAPI(
            `tekephone/${smartphone._id}`,
            {
                ...telephoneData,
                images: [...imgOldUrl, ...media]
            },
            auth.token
        );

        dispatch({ type: POST_TYPES_TELEFONOS.UPDATE_POST_TELEFONO, payload: res.data.newPost });
        dispatch({ type: GLOBALTYPES.ALERT, payload: { success: res.data.msg } });

    } catch (err) {
         dispatch({
            type: GLOBALTYPES.ALERT,
            payload: { error: err.response ? err.response.data.msg : err.message }
        });
    }
};
 
export const createPostSmartphone = ({ telephoneData, images, auth, socket }) => async (dispatch) => {
   

    let media = [];
    try {
        dispatch({ type: GLOBALTYPES.ALERT, payload: { loading: true } });

        // Subir imgenes si hay alguna
        if (images.length > 0) media = await imageUpload(images);

        // Enviar datos al servidor
        const res = await postDataAPI(
            'telefonos',
            { ...telephoneData, images: media },
            auth.token
        );

        // Actualizar el store con el nuevo post
        dispatch({
            type: POST_TYPES_TELEFONOS.CREATE_POST_TELEFONO,
            payload: { ...res.data.newPost, user: auth.user }
        });

        dispatch({ type: GLOBALTYPES.ALERT, payload: { loading: false } });

        // Crear notificacin
        const msg = {
            id: res.data.newPost._id,
            text: 'added a new post.',
            recipients: res.data.newPost.user.followers,
            url: `/post/${res.data.newPost._id}`,
            subCategorySmartphone: telephoneData.subCategorySmartphone, // Usar `postData.content` para el texto
            image: media[0]?.url // Usar la primera imagen, si existe
        };

        dispatch(createNotify({ msg, auth, socket }));
    } catch (err) {
        const errorMsg = err.response?.data?.msg || "An unexpected error occurred";

        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: { error: errorMsg },
        });

        console.error("Error in createPost:", err);
    }
};

export const getPostsSmartphone = (token) => async (dispatch) => {
    try {
        dispatch({ type: POST_TYPES_TELEFONOS.LOADING_POST, payload: true })
        const res = await getDataAPI('telefonos', token)
        
        dispatch({
            type: POST_TYPES_TELEFONOS.GET_POSTS_TELEFONOS,
            payload: { ...res.data, page: 2 }
        })

        dispatch({ type: POST_TYPES_TELEFONOS.LOADING_POST, payload: false })
    } catch (err) {
        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: { error: err.response.data.msg }
        })
    }
}
 
 
export const likePost = ({ post, auth, socket }) => async (dispatch) => {
    const newPost = { ...post, likes: [...post.likes, auth.user] }
    dispatch({ type: POST_TYPES_TELEFONOS.UPDATE_POST, payload: newPost })

    socket.emit('likePost', newPost)

    try {
        await patchDataAPI(`post/${post._id}/like`, null, auth.token)

        // Notify
        const msg = {
            id: auth.user._id,
            text: 'like your post.',
            recipients: [post.user._id],
            url: `/post/${post._id}`,
            content: post.content,
            image: post.images[0].url
        }

        dispatch(createNotify({ msg, auth, socket }))

    } catch (err) {
        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: { error: err.response.data.msg }
        })
    }
}

export const unLikePost = ({ post, auth, socket }) => async (dispatch) => {
    const newPost = { ...post, likes: post.likes.filter(like => like._id !== auth.user._id) }
    dispatch({ type: POST_TYPES_TELEFONOS.UPDATE_POST, payload: newPost })

    socket.emit('unLikePost', newPost)

    try {
        await patchDataAPI(`post/${post._id}/unlike`, null, auth.token)

        // Notify
        const msg = {
            id: auth.user._id,
            text: 'like your post.',
            recipients: [post.user._id],
            url: `/post/${post._id}`,
        }
        dispatch(removeNotify({ msg, auth, socket }))

    } catch (err) {
        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: { error: err.response.data.msg }
        })
    }
}

export const getPost = ({ detailPost, id, auth }) => async (dispatch) => {
    if (detailPost.every(post => post._id !== id)) {
        try {
            const res = await getDataAPI(`post/${id}`, auth.token)
            dispatch({ type: POST_TYPES_TELEFONOS.GET_POSTS_TELEFONO, payload: res.data.post })
        } catch (err) {
            dispatch({
                type: GLOBALTYPES.ALERT,
                payload: { error: err.response.data.msg }
            })
        }
    }
}

export const deletePost = ({ post, auth, socket }) => async (dispatch) => {
    dispatch({ type: POST_TYPES_TELEFONOS.DELETE_POST_TELEFONO, payload: post })

    try {
        const res = await deleteDataAPI(`post/${post._id}`, auth.token)

        // Notify
        const msg = {
            id: post._id,
            text: 'added a new post.',
            recipients: res.data.newPost.user.followers,
            url: `/post/${post._id}`,
        }
        dispatch(removeNotify({ msg, auth, socket }))

    } catch (err) {
        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: { error: err.response.data.msg }
        })
    }
}

export const savePost = ({ post, auth }) => async (dispatch) => {
    const newUser = { ...auth.user, saved: [...auth.user.saved, post._id] }
    dispatch({ type: GLOBALTYPES.AUTH, payload: { ...auth, user: newUser } })

    try {
        await patchDataAPI(`savePost/${post._id}`, null, auth.token)
    } catch (err) {
        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: { error: err.response.data.msg }
        })
    }
}

export const unSavePost = ({ post, auth }) => async (dispatch) => {
    const newUser = { ...auth.user, saved: auth.user.saved.filter(id => id !== post._id) }
    dispatch({ type: GLOBALTYPES.AUTH, payload: { ...auth, user: newUser } })

    try {
        await patchDataAPI(`unSavePost/${post._id}`, null, auth.token)
    } catch (err) {
        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: { error: err.response.data.msg }
        })
    }
}

export const getTotalPostsCountUser = ({ auth, user }) => async (dispatch) => {
    try {
        const res = await getDataAPI(`user/${user._id}/count-posts`, auth.token);
        dispatch({
            type: 'GET_TOTAL_POSTS_USER',
            payload: res.data.counTotalPostsUser // Asumiendo que el servidor devuelve el nmero total en "totalPosts"
        });
    } catch (err) {
        console.error(err);
    }
};


