import { useEffect } from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'

import PageRender from './customRouter/PageRender'
import PrivateRouter from './customRouter/PrivateRouter'

import Home from './pages/home'
import Login from './pages/login'
import Register from './pages/register'

import Alert from './components/alert/Alert'
import Header from './components/header/Header'

import { useDispatch, useSelector } from 'react-redux'
import { refreshToken } from './redux/actions/authAction'

import { getActiveUsersLast24h, getActiveUsersLast3h, getUsers } from './redux/actions/userAction'

import io from 'socket.io-client'
import { GLOBALTYPES } from './redux/actions/globalTypes'
import SocketClient from './SocketClient'


import CallModal from './components/message/CallModal'

import Lastusers from './pages/users/lastusers'
import Searchusers from './pages/users/searchusers'

import Postspendientes from './pages/administracion/postspendientes'

import { getcountpostspendientes, getPostsPendientesss } from './redux/actions/postaproveAction'


import Activityusers from './pages/users/activityusers'



import statusCategoriesFormsImmobilier from './statusCategoriesForms/statusCategoriesFormsImmobilier'

import { getPostsLocation } from './redux/actions/categoriesAction/immobilier/ImmobilierLocationAction'
import { getCategories, getPosts } from './redux/actions/postAction'
import { getPostsSmartphone } from './redux/actions/categoriesAction/telephones/smartphone'
import statusCategoriesFormsTelephones from './statusCategoriesForms/statusCategoriesFormsTelephones'
import { getPostsVente } from './redux/actions/categoriesAction/immobilier/ImmobilierVenteAction'


function App() {
  const { auth, status, modal, call, statusTelephoneReducer, statusImmobilierReducer } = useSelector(state => state)
  const statusImmobilier = statusImmobilierReducer
  const statusTelephone = statusTelephoneReducer

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(refreshToken())

    const socket = io()
    dispatch({ type: GLOBALTYPES.SOCKET, payload: socket })
    return () => socket.close()
  }, [dispatch])

  useEffect(() => {
    if (auth.token) {
      //  dispatch(getTotalPostsCountUser(auth.token))
     // dispatch(getActiveUsersLast3h(auth.token))
// dispatch(getActiveUsersLast24h(auth.token))
  // dispatch(getcountpostspendientes(auth.token))
  dispatch(getPostsVente(auth.token))
      dispatch(getUsers(auth.token))
     getPosts(auth.token)
     // dispatch(getPostsSmartphone(auth.token))
       
      //  dispatch(getPosts(auth.token ) ) // Solo llama si category tiene un valor vlido
       
     
      
 // dispatch(getPostsPendientesss(auth.token));
    }
  }, [dispatch, auth.token])


  useEffect(() => {
    if (!("Notification" in window)) {
      alert("This browser does not support desktop notification");
    }
    else if (Notification.permission === "granted") { }
    else if (Notification.permission !== "denied") {
      Notification.requestPermission().then(function (permission) {
        if (permission === "granted") { }
      });
    }
  }, [])


  return (
    <Router>
      <Alert />

      <input type="checkbox" id="theme" />
      <div className={`App ${(status || modal) && 'mode'}`}>
        <div className="main">
          {auth.token && <Header />}

          {Object.entries(statusImmobilier).map(([category, isActive]) => (
            isActive && statusCategoriesFormsImmobilier[category]
          ))}

          {Object.entries(statusTelephone).map(([category, isActive]) => (
            isActive && statusCategoriesFormsTelephones[category]
          ))}
          {auth.token && <SocketClient />}
          {call && <CallModal />}

          <Route exact path="/" component={auth.token ? Home : Login} />
          <Route exact path="/register" component={Register} />

          <Route exact path="/users/lastusers" component={auth.token ? Lastusers : Login} />
          <Route exact path="/users/searchusers" component={auth.token ? Searchusers : Login} />
          <Route exact path="/users/activityusers" component={auth.token ? Activityusers : Login} />

          <Route exact path="/administracion/postspendientes" component={Postspendientes} />



          <PrivateRouter exact path="/:page" component={PageRender} />
          <PrivateRouter exact path="/:page/:id" component={PageRender} />

        </div>
      </div>
    </Router>
  );
}

export default App;
